import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import { constLang } from "../../../constant/language";
import { createUser } from "../../../redux/user/apiCalls";
import "./newUser.css";

export default function NewUser() {
  const { currentUser } = useSelector(state => state.user)
  const [form, setForm] = useState({})

  const dispatch = useDispatch()
  const navigate = useNavigate()

  const handleChange = ({ target }) => {
    const { value, name } = target
    setForm((prevState) => ({ ...prevState, [name]: value }))
  }

  const handleSubmit = async (ev) => {
    ev.preventDefault()
    await createUser(dispatch, form, currentUser)
    navigate('/admin/users')
  }

  return (
    <div className="newUser">
      <h1 className="newUserTitle">New User</h1>
      <form className="newUserForm" onSubmit={handleSubmit}>
        <div className="newUserItem">
          <label>Username</label>
          <input type="text" placeholder="john" name={constLang.USERNAME} onChange={handleChange} />
        </div>
        <div className="newUserItem">
          <label>Full Name</label>
          <input type="text" placeholder="John Smith" name={constLang.FULLNAME} onChange={handleChange} />
        </div>
        <div className="newUserItem">
          <label>Email</label>
          <input type="email" placeholder="john@gmail.com" name={constLang.EMAIL} onChange={handleChange} />
        </div>
        <div className="newUserItem">
          <label>Password</label>
          <input type="password" placeholder="password" name={constLang.PASSWORD} onChange={handleChange} />
        </div>
        <div className="newUserItem">
          <label>Phone</label>
          <input type="text" placeholder="+1 123 456 78" name={constLang.PHONE} onChange={handleChange} />
        </div>
        <div className="newUserItem">
          <label>Address</label>
          <input type="text" placeholder="New York | USA" name={constLang.ADDRESS} onChange={handleChange} />
        </div>
        <div className="newUserItem">
          <label>Gender</label>
          <div className="newUserGender">
            <input type="radio" name={constLang.GENDER} id="male" value="male" onChange={handleChange} />
            <label htmlFor="male">Male</label>
            <input type="radio" name={constLang.GENDER} id="female" value="female" onChange={handleChange} />
            <label htmlFor="female">Female</label>
            <input type="radio" name={constLang.GENDER} id="other" value="other" onChange={handleChange} />
            <label htmlFor="other">Other</label>
          </div>
        </div>
        <div className="newUserItem">
          <label>Active</label>
          <select className="newUserSelect" name={constLang.ACTIVE} id="active" onChange={handleChange} >
            <option value="true">Yes</option>
            <option value="false">No</option>
          </select>
        </div>
        <button className="newUserButton">Create</button>
      </form>
    </div>
  );
}
