import { CategoryEP, ContactsEP } from "../config/appConfig"
import { httpService } from "./http.service"

export const categoryService = {
    query,
    removeCategory,
    updateCategory,
    createCategory,
}

export async function query(filterBy = {}) {
    try {
        const res = await httpService.get(CategoryEP.default)
        return res
    } catch (err) {
        throw err
    }
}

async function removeCategory(catId, currentUser) {
    try {
        const contact = { id: catId, user: currentUser }
        return httpService.post(`${ContactsEP.default}/${catId}`, contact)
    } catch (err) {
        throw err
    }
}

async function updateCategory(category) {
    try {
        if (category._id) {
            const content = { category }
            const res = await httpService.post(`${CategoryEP.update}/${category._id}`, content)
            if (res.status !== 'ok') throw new Error("Couldn't save contact")
        }
    } catch (err) {
        throw err
    }
}

async function createCategory(category) {
    try {
        const content = { category }
        const res = await httpService.post(`${CategoryEP.create}`, content)
        if (res.status === 'ok') return res.content
        else throw new Error("Couldn't create category")
    } catch (err) {
        throw err
    }
}