import { createContact } from "../../../redux/contact/apiCalls";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import { JsConst } from "../../../constant/JsConst";
import { OptionsSelect } from "../../../components/OptionsSelect";
import { Modal } from "../../../components/Modal";
import { categoryService } from "../../../services/category.service";
import { utilService } from "../../../services/util.service";


export default function NewContact() {
    const { currentUser } = useSelector(state => state.user)

    const [modalProps, setModalProps] = useState(null)
    const [categories, setCategories] = useState(null)
    const [inputs, setInputs] = useState({})
    const [file, setFile] = useState(null)

    const dispatch = useDispatch();
    const navigate = useNavigate()

    useEffect(() => {
        loadCategories()
    }, [])

    const loadCategories = async () => {
        const categories = await categoryService.query()
        if (categories.status === 'ok') setCategories(categories.content)
        else setCategories(null)
    }

    const handleChange = ({ target }) => {
        let { name, value } = target
        value = handleCat(name, value)
        setInputs((prev) => {
            return { ...prev, [name]: value };
        });
    };

    const handleSubmit = async (ev) => {
        ev.preventDefault()
        await createContact(dispatch, inputs, currentUser)
        navigate('/admin/contacts')
    }

    const handleCat = (name, value) => {
        if (name === JsConst.CATEGORIES ||
            name === JsConst.COUNTRY ||
            name === JsConst.PHONE ||
            name === JsConst.LINKEDIN_LINKS
        ) return value.split(",")
        else return value
    };

    const handleClick = (e) => {
        // e.preventDefault();
        // const fileName = new Date().getTime() + file.name;
        // const storage = getStorage(app);
        // const storageRef = ref(storage, fileName);
        // const uploadTask = uploadBytesResumable(storageRef, file);

        // Register three observers:
        // 1. 'state_changed' observer, called any time the state changes
        // 2. Error observer, called on failure
        // 3. Completion observer, called on successful completion
        // uploadTask.on(
        // "state_changed",
        // (snapshot) => {
        // Observe state change events such as progress, pause, and resume
        // Get task progress, including the number of bytes uploaded and the total number of bytes to be uploaded
        // const progress =
        // (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
        // console.log("Upload is " + progress + "% done");
        // switch (snapshot.state) {
        // case "paused":
        // console.log("Upload is paused");
        // break;
        // case "running":
        // console.log("Upload is running");
        // break;
        // default:
        // }
        // },
        // (error) => {
        // Handle unsuccessful uploads
        // },
        // () => {
        // Handle successful uploads on complete
        // For instance, get the download URL: https://firebasestorage.googleapis.com/...
        // getDownloadURL(uploadTask.snapshot.ref).then((downloadURL) => {
        //     const product = { ...inputs, img: downloadURL, categories: cat };
        //     addProduct(product, dispatch);
        // });
        // }
        // );
    };

    const getModalProps = () => {
        return {
            setModalProps,
            categories
        }
    }

    const getCategories = (type) => {
        if (categories) {
            return categories[type]
        } else return null
    }

    return (
        <div className="newContact">
            <h1 className="addContactTitle">New Contact</h1>
            <form className="addContactForm" onSubmit={handleSubmit}>
                <div className="addContactItem">
                    <div className="label-container">
                        <label>Company</label>
                        <span onClick={() => setModalProps({ type: 'company', idx: 3 })}>Add New</span>
                    </div>
                    <OptionsSelect options={getCategories(3)} cb={handleChange} />
                </div>

                <div className="addContactItem">
                    <label>Name</label>
                    <input name={JsConst.NAME} type="text" placeholder="John" onChange={handleChange} />
                </div>
                <div className="addContactItem">
                    <label>Family Name</label>
                    <input name={JsConst.FAMILY_NAME} type="text" placeholder="John" onChange={handleChange} />
                </div>
                <div className="addContactItem">
                    <div className="label-container">
                        <label>Job Titles</label>
                        <span onClick={() => setModalProps({ type: 'jobTitle', idx: 0 })}>Add New</span>
                    </div>
                    <OptionsSelect options={getCategories(0)} cb={handleChange} />
                </div>
                <div className="addContactItem">
                    <div className="label-container">
                        <label>Territories</label>
                        <span onClick={() => setModalProps({ type: 'territory', idx: 1 })}>Add New</span>
                    </div>
                    <OptionsSelect options={getCategories(1)} cb={handleChange} />
                </div>
                <div className="addContactItem">
                    <label>Description</label>
                    <input name={JsConst.DESC} type="text" placeholder="description..." onChange={handleChange} />
                </div>
                <div className="addContactItem">
                    <label>Email</label>
                    <input name={JsConst.EMAIL} type="text" placeholder="john@gmail.com" onChange={handleChange} />
                </div>
                <div className="addContactItem">
                    <label>Phone</label>
                    <input name={JsConst.PHONE} type="text" placeholder="+972.." onChange={handleChange} />
                </div>
                <div className="addContactItem">
                    <label>Price</label>
                    <input name={JsConst.PRICE} type="number" placeholder="100" onChange={handleChange} />
                </div>
                <div className="addContactItem">
                    <div className="label-container">
                        <label>Categories</label>
                        <span onClick={() => setModalProps({ type: 'category', idx: 2 })}>Add New</span>
                    </div>
                    <OptionsSelect options={getCategories(2)} cb={handleChange} />
                </div>
                <div className="addContactItem">
                    <label>LinkedIn Links</label>
                    <input name={JsConst.LINKEDIN_LINKS} type="text" onChange={handleChange} />
                </div>
                <div className="addContactItem">
                    <label>In Stock</label>
                    <select name={JsConst.IN_STOCK} onChange={handleChange}>
                        <option value="true">Yes</option>
                        <option value="false">No</option>
                    </select>
                </div>
                <div className="addContactItem">
                    <label>Image</label>
                    <input type="file" id="file" onChange={(e) => setFile(e.target.files[0])} />
                </div>
                <div className="add-contact-button--container">
                    <button onClick={handleClick} className="addContactButton">
                        Create
                    </button>
                </div>
            </form>
            {
                modalProps && <Modal {...getModalProps()} >
                    <AddNewCategoryModal {...modalProps} {...getModalProps()} />
                </Modal>
            }
        </div>
    );
}

const AddNewCategoryModal = (porps) => {
    const { type, setModalProps, categories, idx } = porps
    const [form, setForm] = useState({
        type,
        _id: categories[idx]._id
    })

    const updateCategory = async () => {
        const selectedCat = { ...categories[idx] }
        const categoryVal = {
            value: form.value,
            label: form.value,
        }
        selectedCat.categories.push(categoryVal)
        await categoryService.createCategory(selectedCat)
    }

    const handleChange = ({ target }) => {
        const { value } = target
        setForm((prevState) => ({ ...prevState, value: value }))
    }

    const onSubmit = async (ev) => {
        ev.preventDefault()
        updateCategory()
        setModalProps(null)
    }

    return (
        <form onSubmit={onSubmit} className="option-modal-container">
            <h2>Add new {type}</h2>
            <div className="option-modal-inputs-container">
                <label>{utilService.getFirstLetterUppercase(type)} Name</label>
                <input type="text" onChange={handleChange} />
            </div>
            <div>
                <button className="btn primary-btn">Save</button>
            </div>
        </form>
    )
}