import { constContact } from "./constContact"


export class Contact {
    _id
    desc
    img
    jobTitle
    price
    inStock
    name
    familyName
    email
    mobile
    phone
    linkedinLink
    agent
    category
    country
    company

    constructor(contact) {
        this._id = contact[constContact.ID]
        this.desc = contact[constContact.DESCRIPTION]
        this.img = contact[constContact.IMG]
        this.jobTitle = contact[constContact.JOBTITLE]
        this.price = contact[constContact.PRICE]
        this.inStock = contact[constContact.IN_STOCK]
        this.name = contact[constContact.NAME]
        this.familyName = contact[constContact.FAMILY_NAME]
        this.email = contact[constContact.EMAIL]
        this.mobile = contact[constContact.MOBILE]
        this.phone = contact[constContact.PHONE]
        this.linkedinLink = contact[constContact.LINKEDIN_LINK]
        this.agent = contact[constContact.AGENT]
        this.category = contact[constContact.CATEGORY]
        this.country = contact[constContact.COUNTRY]
        this.company = contact[constContact.COMPANY]
    }
}