import { httpService } from './http.service'

const STORAGE_KEY_LOGGEDIN_USER = 'loggedinUser'
export const userService = {
    login,
    logout,
    signup,
    createUser,
    updateUser,
    getUsersStats,
    isAdmin,
    getLoggedinUser,
    saveLocalUser,
    getUsers,
    getById,
    remove,
    update,
}


async function getUsers(currentUser, filterBy = {}) {
    try {
        const content = { user: currentUser, filterBy }
        return await httpService.post(`users`, content)
    } catch (err) {
        throw new Error('Cannot get users')
    }
}

async function getUsersStats(currentUser) {
    try {
        return await httpService.post(`users/stats`, currentUser)
    } catch (err) {
        console.log(err)
    }
}

async function getById(userId) {
    try {
        const user = await httpService.post(`users/find/${userId}`, { userId })
        return user
    } catch (err) {
        throw new Error('Cannot find user')
    }
}

async function remove(userId, currentUser) {
    try {
        const content = { userId, user: currentUser }
        const res = await httpService.post(`users/remove/${userId}`, content)
        if (res.status === 'ok') return res.content
        else throw new Error('Cannot delete user')
    } catch (err) {
        throw err
    }
}

async function update(user) {
    user = await httpService.put(`user/${user._id}`, user)
    if (getLoggedinUser()._id === user._id) saveLocalUser(user)
    return user;
}

async function login(userCred) {
    try {
        const user = await httpService.post('auth/login', userCred)
        if (user) {
            return saveLocalUser(user)
        }
    } catch (err) {
        console.log(err)
    }
}

async function signup(userCred) {
    try {
        const res = await httpService.post('auth/register', userCred)
        return res
    } catch (err) {
        throw err
    }
}

async function isAdmin(userCred) {
    try {
        const content = { user: userCred }
        const res = await httpService.post('auth/isAdmin', content)
        if (res.status === 'ok') return 'ok'
    } catch (err) {
        console.log(err)
    }
}

async function createUser(userCred, currentUser) {
    try {
        const content = { userCred, user: currentUser }
        const res = await httpService.post('users/create', content)
        if (res.status === 'ok') return res.content
        else throw new Error('Cannot create user')
    } catch (err) {
        throw err
    }
}

async function updateUser(userCred, currentUser) {
    try {
        const content = { userCred, user: currentUser }
        const res = await httpService.post('users/update', content)
        if (res.status === 'ok') return res.content
        else throw new Error('Cannot update user')
    } catch (err) {
        throw err
    }
}

function logout() {
    sessionStorage.removeItem(STORAGE_KEY_LOGGEDIN_USER)
    return httpService.post('auth/logout')
}


function saveLocalUser(user) {
    sessionStorage.setItem(STORAGE_KEY_LOGGEDIN_USER, JSON.stringify(user))
    return user
}

function getLoggedinUser() {
    return JSON.parse(sessionStorage.getItem(STORAGE_KEY_LOGGEDIN_USER))
}

