import { FavoriteBorderOutlined, SearchOutlined, ShoppingCartOutlined } from "@material-ui/icons";
import { useState } from "react";
import { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Link, useNavigate } from "react-router-dom";
import styled from "styled-components";
import { addContact } from "../redux/cartRedux";
import { addFavorite, deleteFavorite } from "../redux/user/apiCalls";
import { userService } from "../services/user.service";

const Info = styled.div`
  opacity: 0;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.2);
  z-index: 3;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.5s ease;
  cursor: pointer;
`;

const Container = styled.div`
  flex: 1;
  margin: 5px;
  min-width: 280px;
  height: 350px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #f5fbfd;
  position: relative;

  &:hover ${Info}{
    opacity: 1;
  }
`;

const Circle = styled.div`
  width: 200px;
  height: 200px;
  border-radius: 50%;
  background-color: white;
  position: absolute;
`;

const Image = styled.img`
  height: 75%;
  z-index: 2;
  width: 100%;
  aspect-ratio: 3/2;
  object-fit: cover;
`;

const Icon = styled.div`
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 10px;
  transition: all 0.5s ease;
  &:hover {
    background-color: #e9f5f5;
    transform: scale(1.1);
  }
  &.active {
    color: red;
  }
`;

const Contact = ({ item }) => {
  const { currentUser } = useSelector((state) => state.user)

  const [isFavorite, setIsFavorite] = useState(false)

  useEffect(() => {
    loadFavorite()
    // checkIsFavorite()
  }, [])

  const dispatch = useDispatch()
  const navigate = useNavigate()

  const onAddToCart = () => {
    dispatch(addContact(item))
  }

  const onAddToFavorite = (item) => {
    addFavorite(dispatch, item, currentUser)
  }

  const onRemoveFavorite = (item) => {
    deleteFavorite(dispatch, item, currentUser)
  }

  const loadFavorite = () => {
    if (currentUser) {
      const isFavorite = currentUser.favorites.find(favorite => favorite._id === item._id)
      if (isFavorite) setIsFavorite(true)
      else setIsFavorite(false)
    }
  }

  const onFavorite = (item) => {
    if (isFavorite) {
      onRemoveFavorite(item)
      setIsFavorite(false)
    }
    else {
      onAddToFavorite(item)
      setIsFavorite(true)
    }
  }

  return (
    <Container>
      <Circle />
      <Image src={item.img} />
      <Info>
        <Icon onClick={onAddToCart}>
          <ShoppingCartOutlined />
        </Icon>
        <Icon onClick={() => navigate(`/contact/${item._id}`)}>
          <SearchOutlined />
        </Icon>
        <Icon onClick={() => onFavorite(item)} className={`${isFavorite ? 'active' : ''}`}>
          <FavoriteBorderOutlined />
        </Icon>
      </Info>
    </Container>
  );
};

export default Contact;
