import { deleteUser, insertUser, loginFailure, loginStart, loginSuccess, logoutSuccess, removeFavorite, saveFavorite, saveUser, setUsers } from "./userRedux";
import { userService } from "../../services/user.service";

export const login = async (dispatch, user) => {
  dispatch(loginStart());
  try {
    const loggedInUser = await userService.login(user)
    dispatch(loginSuccess(loggedInUser));
    return loggedInUser
  } catch (err) {
    dispatch(loginFailure());
  }
};

export const signup = async (dispatch, cred) => {
  try {
    const res = await userService.signup(cred)
    console.log(res)
    if (res.status === 'ok') {
      const user = await userService.login(cred)
      dispatch(loginSuccess(user));
      return user
    }
  } catch (err) {
    throw err
  }
}

export const logout = async (dispatch) => {
  try {
    await userService.logout()
    dispatch(logoutSuccess())
  } catch (err) {

  }
}

export const requiredAdmin = async (dispatch, user, navigate) => {
  try {
    const res = await userService.isAdmin(user)
    if (res === 'ok') navigate('/admin')
  } catch (err) {

  }
}

export const getUsers = async (dispatch, currentUser) => {
  try {
    const users = await userService.getUsers(currentUser)
    dispatch(setUsers(users))
  } catch (err) {
    console.log('getUsers =>> ', err)
  }
}

export const removeUser = async (dispatch, id, currentUser) => {
  try {
    const res = await userService.remove(id, currentUser)
    dispatch(deleteUser(res))
  } catch (err) {
    console.log('getUsers =>> ', err)
  }
}

export const getUsersStats = async (dispatch, currentUser) => {
  try {
    const usersStats = await userService.getUsersStats(currentUser)
    // dispatch(setUsers(users))
  } catch (err) {
    console.log('getUsers =>> ', err)
  }
}

export const createUser = async (dispatch, userCred, currentUser) => {
  try {
    const user = await userService.createUser(userCred, currentUser)
    dispatch(insertUser(user))
  } catch (err) {
    console.log('createUser =>> ', err)
  }
}

export const updateUser = async (dispatch, userCred, currentUser) => {
  try {
    const user = await userService.updateUser(userCred, currentUser)
    dispatch(saveUser(user))
  } catch (err) {
    console.log('createUser =>> ', err)
  }
}

export const addFavorite = async (dispatch, item, currentUser) => {
  try {

    const userCred = { ...currentUser, favorites: [...currentUser.favorites, item] }
    await userService.updateUser(userCred, currentUser)
    dispatch(saveFavorite(item))
  } catch (err) {
    console.log('add to favorite failed ==>> ', err)
  }
}

export const deleteFavorite = async (dispatch, item, currentUser) => {
  try {
    const userCred = { ...currentUser, favorites: currentUser.favorites.filter(favoriteId => favoriteId._id !== item._id) }
    await userService.updateUser(userCred, currentUser)
    dispatch(removeFavorite(item))
  } catch (err) {
    console.log('add to favorite failed ==>> ', err)
  }
}