import "./widgetSm.css";
import { Visibility } from "@mui/icons-material";
import { useEffect, useState } from "react";
import { userService } from "../../../services/user.service";
import { useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";

export default function WidgetSm() {
  const { currentUser } = useSelector((state) => state.user)
  const navigate = useNavigate()

  const [users, setUsers] = useState([])
  const [loading, setLoading] = useState([])
  const [error, setError] = useState(false)

  useEffect(() => {
    loadUsers()
  }, [])

  const getRecentRegisteredUsers = () => {
    const now = new Date();
    const dayOfWeek = now.getDay();
    const startOfWeek = new Date(now.getFullYear(), now.getMonth(), now.getDate() - dayOfWeek);
    const endOfWeek = new Date(now.getFullYear(), now.getMonth(), now.getDate() - dayOfWeek + 7);
    return { startOfWeek, endOfWeek }
  }

  const loadUsers = async () => {
    const { startOfWeek, endOfWeek } = getRecentRegisteredUsers()
    const filterBy = { createdAt: { startOfWeek, endOfWeek } }
    setLoading(true)
    try {
      const users = await userService.getUsers(currentUser, filterBy)
      setUsers(users)
      setLoading(false)
    } catch (err) {
      setError(true)
    }
  }

  return (
    <div className="widgetSm">
      <span className="widgetSmTitle">New Join Members</span>
      {(!loading || !error) && <ul className="widgetSmList">
        {users.map((user) => (
          <li className="widgetSmListItem" key={user._id}>
            <img
              src={
                user.img ||
                "https://crowd-literature.eu/wp-content/uploads/2015/01/no-avatar.gif"
              }
              alt=""
              className="widgetSmImg"
            />
            <div className="widgetSmUser">
              <span className="widgetSmUsername">{user.username}</span>
            </div>
            <button className="widgetSmButton" onClick={() => navigate(`/admin/user/${user._id}`)}>
              <Visibility className="widgetSmIcon" />
              Display
            </button>
          </li>
        ))}
      </ul>}
      {!error && loading && <div className="widgetSmList">Loading...</div>}
      {error && <div className="widgetSmList">Something wrong</div>}
    </div>
  );
}
