import { createSlice } from "@reduxjs/toolkit";
import { constLang } from "../../constant/language";
import { userService } from "../../services/user.service";

const userSlice = createSlice({
  name: constLang.user,
  initialState: {
    currentUser: userService.getLoggedinUser(),
    users: [],
    isFetching: false,
    error: false,
  },
  reducers: {
    setUsers: (state, action) => {
      state.users = action.payload;
    },
    insertUser: (state, action) => {
      state.users.push(action.payload)
    },
    saveUser: (state, action) => {
      state.users = state.users.map(currUser =>
        (currUser._id === action.payload._id) ? { ...action.payload } : currUser)
    },
    deleteUser: (state, action) => {
      state.users = state.users.filter((item) => item._id !== action.payload)
    },
    loginStart: (state) => {
      state.isFetching = true;
    },
    loginSuccess: (state, action) => {
      state.isFetching = false;
      state.currentUser = action.payload;
    },
    loginFailure: (state) => {
      state.isFetching = false;
      state.error = true;
    },
    logoutSuccess: (state) => {
      state.currentUser = null
    },
    saveFavorite: (state, action) => {
      state.currentUser.favorites.push(action.payload)
    },
    removeFavorite: (state, action) => {
      state.currentUser.favorites = state.currentUser.favorites.filter(favoriteId => favoriteId._id !== action.payload._id)
    }
  },
});

export const { loginStart, loginSuccess, loginFailure, logoutSuccess, setUsers, deleteUser, insertUser, saveUser, saveFavorite, removeFavorite } = userSlice.actions;
export default userSlice.reducer;
