import { useEffect, useState } from "react";
import styled from "styled-components";
import { contactService } from "../services/contact.service";
import Contact from "./Contact";

const Container = styled.div`
  padding: 20px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
`;

const Contacts = ({ cat, filters, sort }) => {
  const [contacts, setContacts] = useState([]);
  const [filteredContacts, setFilteredContacts] = useState([]);

  useEffect(() => {
    loadContacts()
  }, []);

  useEffect(() => {
    cat &&
      setFilteredContacts(
        contacts.filter((item) =>
          Object.entries(filters).every(([key, value]) => {
            console.log(value)
            return item[key] === value
          }
          )
        )
      );
  }, [contacts, cat, filters]);

  useEffect(() => {
    if (sort === "newest") {
      setFilteredContacts((prev) =>
        [...prev].sort((a, b) => a.createdAt - b.createdAt)
      );
    } else if (sort === "asc") {
      setFilteredContacts((prev) =>
        [...prev].sort((a, b) => a.price - b.price)
      );
    } else {
      setFilteredContacts((prev) =>
        [...prev].sort((a, b) => b.price - a.price)
      );
    }
  }, [sort]);

  const loadContacts = async () => {
    let data;
    if (cat) {
      const res = await contactService.query()
      data = res.filter(contact => contact.category.toLowerCase() === cat)
    }
    else data = await contactService.query()
    setContacts(data)
  };

  return (
    <Container>
      {cat
        ? filteredContacts.map((item) => <Contact item={item} key={item._id} />)
        // : contacts.slice(0, 8).map((item) => <Contact item={item} key={item._id} />)}
        : contacts.map((item) => <Contact item={item} key={item._id} />)}
    </Container>
  );
};

export default Contacts;
